<template>
  <div id="index">

    <div class="top">
      <div class="item">
        采购
      </div>
      <div class="item">
        订单
      </div>
    </div>

    <div class="rebate">
      <div class="left">
        <span>
          水厂返利 (元)
        </span><br>
        <span>
          8888
        </span>
      </div>
      <div class="right">
        <img src="../image/index/index_rebate.png" alt="">
      </div>
    </div>
    <div class="ad">
      <div class="left">充值优惠</div>
      <div class="middle">
        <span>冲的越多  送的越多</span><br>
        <span>冲一百送一百！</span>
      </div>
      <div class="right">
      <span class="icon">new</span>
      >
      </div>
    </div>

    <div class="service">
      <div class="title">你的服务</div>
      <div class="box">
        <div class="item">
        政策签约
        </div>
        <router-link :to="{path:'Custom',query: {}}">
          <div class="item">客户管理</div>
        </router-link>
        <div class="item">
        水来学院
        </div>
        <div class="item">
        员工管理
        </div>
        <div class="item">
        店铺推广
        </div>
        <div class="item">
        商品管理
        </div>
      </div>
    </div>

  </div> 
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      msg: 'Welcome to Your Test'
    }
  },
  created: function () {
    console.log(this.$route)
  }
}
</script>

<style scoped>
#index{
  font-size: 14px;

}
.top{
  background:#0097E0;
  height:10em;
  line-height: 10em;
}
.section{
  width:100%;

}
.top .item{
  width:50%;
  font-size:1.5em;
  color:white;
  text-align: center;
  display: inline-block;
  float: left;
}
.rebate{
  height: 8em;
  line-height: 8em;
  padding: 0 1.5em;
  vertical-align: middle;
  border-bottom:0.1em solid #f0f0f0;
  overflow: hidden;
}
.rebate .left{
  display: inline-block;
  width: 50%;
  height: 100%;
  float: left;
  font-size: 1.3em;
  line-height: 1.5em;
  padding-top: 2em;
}
.rebate .left span{
  display: inline-block;
  color:#0097E0;
  height: 2px;
}
.rebate .right{
  display: inline-block;
  height: 100%;
  width: 50%;
  float: right;
  text-align: right;
  position: relative;
}
.rebate .right img{
  height:6em;
  position: absolute;
  right: 0em;
  top: 1em;
  /*margin-top: 1em;*/
  /*margin-right: 2em;*/
}
.ad{
  width: 100%;
  height: 5em;
  border-bottom:0.1em solid #f0f0f0;
  position: relative;
  border-bottom:0.3em solid #f0f0f0;
}

.ad .left{
  color:#FE7555;
  height: 100%;
  width: 4em;
  display: inline-block;
  vertical-align: middle;
  font-style: oblique;
  font-weight: 700;
  font-size: 1.4em;
  position: absolute;
  left: 1em;
  top:0;
  padding-top: 1em;
  text-align: center;
}
.ad .middle{
  width: 100%;
  padding-left: 8.5em;
  padding-top: 1em;
  padding-right: 5em;
  box-sizing: border-box;
}
.ad .middle span:nth-child(1){
  font-size: 1.1em;
  font-weight: 600;
  margin-bottom: 0.1px;
}
.ad .middle span:nth-child(3){
  color:#FFA200;
}
.ad .right{
  position: absolute;
  top: 1.8em;
  right: 1em;
  color:#666;
}
.ad .right .icon{
  background: #FE7555;
  color: white;
  width: 3em;
  display: inline-block;
  text-align: center;
  height: 1.3em;
  border-radius: 1em;
}

.service{

}
.service .title{
  height:3em;
  width:100%;
  line-height: 3em;
  padding-left:1em;
  font-weight: 600;
  font-size: 1.1em;
  box-sizing: border-box;
}
.service .box{

}
.service .box .item{
  height: 8em;
  width: 32.5%;
  display: inline-block;
  text-align: center;
  line-height: 8em;
}
.service .box .item:active{
  background: #f0f0f0;
}
</style>



